import React, {useEffect, useRef, useState} from 'react';
import Breadcrumbs from "../../reusable/Layout/Breadcrumbs";
import {changeCurrentAdminMenuLink, metaTitle} from "../../../utils/misc";
import {formatBytes} from "../../../utils/files";
import {useDispatch, useSelector} from "react-redux";
import {importFile} from "../../../redux/thunks/importFile";
import {toast} from "react-toastify";
import {Icon} from "@iconify/react";
import {decompressJson} from "../../../utils/json";
import Layout from "../../reusable/Layout";
import ActionsBar from "../../reusable/Layout/ActionsBar";
import useTranslation from "../../../hooks/useTranslation";

const Import = () => {

    // manage global state
    const {content, success, loading, errors} = useSelector(state => state.importFileReducer);
    const dispatch = useDispatch();

    // manage local state
    const [uploadedFile, setUploadedFile] = useState(null);
    const didMountRef = useRef(false);

    useEffect(() => {
        metaTitle(useTranslation(`Import custom post types`));
        changeCurrentAdminMenuLink('#/import');
    }, []);

    // handle file upload outcome
    useEffect(() => {
        if (didMountRef.current){
            if(!loading){
                if(success){
                    toast.success(useTranslation("Success! You imported successfully your custom post types."));
                }

                if(errors.length > 0){
                    errors.map((error) => {
                        toast.error(error);
                    });
                }
            }
        } else {
            didMountRef.current = true;
        }
    }, [loading]);

    // handle file upload
    const handleFileUpload = async ( e ) => {
        const file = e.target.files[0];
        const fileContent = await file.text();
        const decompressedFile = new File( [decompressJson( fileContent )], file.name, {
            type: "text/plain",
        } );

        setUploadedFile( decompressedFile );

        let formData = new FormData();
        formData.append( 'file', decompressedFile );

        dispatch( importFile( formData ) );
    };

    const button = (
        <label htmlFor="upload" className="acpt-btn acpt-btn-inline acpt-btn-primary">
            {loading
                ?
                <React.Fragment>
                    {useTranslation("Loading...")}
                </React.Fragment>
                :
                <React.Fragment>
                    {useTranslation("Upload file")}
                </React.Fragment>
            }
        </label>
    );

    return (
        <Layout>
            <ActionsBar
                title={useTranslation("Import data")}
            />
            <main>
                <Breadcrumbs crumbs={[
                    {
                        label: useTranslation("Registered Custom Post Types"),
                        link: "/"
                    },
                    {
                        label:useTranslation("ACPT Tools"),
                        link: "/tools"
                    },
                    {
                        label: useTranslation("Import data")
                    }
                ]} />
                <div className="acpt-card">
                    <div className="acpt-card__header">
                        <div className="acpt-card__inner">
                            {useTranslation("Import an ACPT backup here.")}
                        </div>
                    </div>
                    <div className="acpt-card__body">
                        <div className="acpt-card__inner">
                            <div className="acpt-card__upload">
                                <div className="acpt-card__upload__inner">
                                    <div className="labels">
                                        <h2 className="title">
                                            {useTranslation("Upload your file")}
                                        </h2>
                                        <div className="text-center mb-1 mt-1">
                                            {button}
                                        </div>
                                        <h4 className="label">
                                            {useTranslation("[Allowed extension: *.acpt, max size: 2Mb]")}
                                        </h4>
                                    </div>
                                    {success && uploadedFile && (
                                        <React.Fragment>
                                            <div className="icon-circle mt-2 mb-2" style={{borderColor: "#2271b1" }}>
                                                <Icon width="48px" icon="bx:bx-check" color="#2271b1"/>
                                            </div>
                                            <div className="acpt-card__upload-preview space-between">
                                                <div className="acpt-card__upload-preview__inner">
                                                    <span className="name">
                                                        {uploadedFile.name}
                                                    </span>
                                                    <span className="size">
                                                        {formatBytes(uploadedFile.size)}
                                                    </span>
                                                </div>
                                            </div>
                                        </React.Fragment>
                                    )}
                                    {!content &&
                                    <React.Fragment>
                                        <input
                                            onChange={e => handleFileUpload(e)}
                                            style={{display: "none"}}
                                            name="file"
                                            type="file"
                                            id="upload"
                                        />
                                    </React.Fragment>
                                    }
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </main>
        </Layout>
    );
};

export default Import;